body {
  font-family: "Fira Sans Extra Condensed"; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 50;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.25); }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 3; }
  .header-container {
    max-width: 1500px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding-left: 20px; }
  .header-body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%; }
  .header-logo {
    flex: 0 0 60px;
    position: relative;
    z-index: 4; }
    .header-logo img {
      max-width: 100%;
      display: block; }
  .header-burger {
    display: none; }
  .header-nav {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .header-menu {
    display: flex;
    position: relative;
    z-index: 3; }
    .header-menu li {
      list-style: none;
      margin-right: 30px; }
    .header-menu__link {
      font-size: 18px;
      font-weight: 400;
      text-decoration: none;
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      background-clip: text;
      color: transparent; }
      .header-menu__link:hover {
        color: #99a3ff;
        cursor: pointer; }
    .header-menu__button {
      position: relative;
      font-size: 24px;
      font-weight: 600;
      color: #fff;
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      padding: 35px 20px;
      z-index: 3; }

@media (max-width: 1101px) {
  .header body.lock {
    overflow: hidden; }
  .header-container {
    padding: 30px 20px; }
  .header-logo {
    flex: 0 0 40px; }
  .header-burger {
    display: block;
    position: relative;
    width: 50px;
    height: 35px;
    z-index: 56; }
    .header-burger span {
      position: absolute;
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      left: 0;
      width: 100%;
      height: 3px;
      top: 15px;
      transition: all 0.3s ease 0s; }
    .header-burger::before {
      content: "";
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      transition: all 0.3s ease 0s;
      top: 0; }
    .header-burger::after {
      content: "";
      background: linear-gradient(90deg, #4c9af5 0%, #5640df 50.52%, #9937bc 80.73%);
      position: absolute;
      width: 100%;
      height: 3px;
      left: 0;
      transition: all 0.3s ease 0s;
      bottom: 0; }
    .header-burger.active span {
      transform: scale(0); }
    .header-burger.active::before {
      transform: rotate(45deg);
      top: 14px; }
    .header-burger.active::after {
      transform: rotate(-45deg);
      bottom: 17px; }
  .header-nav {
    display: block;
    position: fixed;
    top: 0;
    left: -100%;
    width: 80%;
    height: 100%;
    overflow: hidden;
    transition: all 0.3s ease 0s;
    background-color: #fff;
    box-shadow: 0px 5px 16px rgba(90, 90, 90, 0.25);
    padding: 150px 0 20px 0;
    z-index: 55; }
    .header-nav.active {
      left: 0; }
  .header-menu {
    display: block;
    padding: 0 10px; }
    .header-menu li {
      margin: 0 0 20px 0; }
    .header-menu__link {
      font-size: 24px; } }

